import React, { Fragment } from 'react';
import { Anchor, Ellipse, Cylinder, Box, Hemisphere, Rect, RoundedRect, Group, Shape, Polygon } from 'react-zdog';

import { AmpShape } from '../shapes/letters';

const DARK_AQUA = "#84DCC6";
const LIGHT_AQUA = "#A5FFD6";
const LIGHT_RED = "#FFA69E";
const DARK_RED = "#FF686B";
const YELLOW = "#FFCE49";

const ShapeExtrusion = ({ fill=true, color="#333", sideColor="#BBB", depth=30, spacing=1.1, stroke=1, ...props }) => (
    <Anchor>
        <Group>
            {[...Array(depth)].map((n, i) => (
                <AmpShape
                    color={ i === (depth - 1)? color : (sideColor? sideColor : color) }
                    translate={{ z: i * spacing }}
                    fill={ fill && (i === 0 || i === (depth - 1)) }
                    stroke={ i === (depth - 1)? 5 : stroke}
                    {...props} />
            ))}
        </Group>
    </Anchor>
);

export default ShapeExtrusion;